body,html{margin:0;padding:0;width:100%;height:100%; font-family: "Poppins", sans-serif;
  font-display: swap;}
body{font-family:"Poppins",sans-serif;font-size:12px;color:#424242}
.skip-link{position:absolute;top:-100px;left:0;background-color:#bf1722;color:white;padding:10px;font-size:20px;z-index:100;text-decoration:none}
.skip-link:focus{top:5px;top:5px}
.header{min-height:58px;transition:min-height 1s;background-color:#2c3e50}
.header__menu{font-size:40px;margin-left:15px;width:50px;text-align:left;color:#fff;background-color:transparent;border:none;display:inline-block}
.logo{width:30%;padding:0;margin-left:30px;text-align:left;box-sizing:border-box;display:inline-block;line-height:24px;font-weight:700}
.nav{background-color:#2c3e50;width:100%;display:flex;font-weight:600;font-size:14px;text-transform:uppercase}
.nav__list{width:80%;padding:0;margin:0;text-align:right}
.nav__item{box-sizing:border-box;display:inline-block;text-align:right;line-height:24px;text-transform:uppercase;font-weight:400}
.nav a{display:inline-block;padding:1.3em;text-decoration:none;color:white!important}
.nav a:hover{text-decoration:underline;color:#000}
.hero{display:flex;align-items:center;min-height:380px;width:100%;text-align:center;background-position:center}
.hero__inner{margin:0 auto;max-width:800px}
.hero__title{color:#fff;font-weight:500;font-size:36px}
.hero__tagline{color:#fff;margin-top:16px;font-size:18px;font-weight:300}
.hide-hero .hero{display:none}
main{width:100%;margin:0 auto}
.content{padding:32px}
.terbaru,.detail,.menu{width:100%;text-align:left}
.terbaru_label,.detail_label,#list-menu,#review-title{margin-left:34px;margin-top:10px}
.terbaru h1,#favoriteList h1,.detail_label,#favorite_label,#list-menu,#review-title{font-weight:700;font-size:2.6em}
.daftar{margin-right:30px;margin-left:30px;margin-bottom:30px;text-align:left;display:grid;grid-row-gap:16px}
.daftar_item{box-shadow:0 4px 8px 0 rgba(0,0,0,0.2);width:100%;border-radius:5px;overflow:hidden;position:relative}
.city{position:absolute;top:5px;left:0px;border-radius:0px 20px 20px 0px;background-color:#2c3e50;padding:5px 20px 5px;color:white;font-weight:500}
.daftar_item_content{padding:16px}
.daftar_item_gambar{width:100%;max-height:200px}
.daftar_item_rating{font-size:12px;text-transform:uppercase;color:#999;font-weight:700}
.daftar_item_rating_value{padding:1.5em;color:red;text-decoration:none;margin-left:-15px}
.favorite{padding:20px;max-width:1200px;margin:auto}
.favorite_label{text-align:center;margin-bottom:20px}
#favoriteList{display:grid;grid-template-columns:1fr;gap:20px}
.restaurant-item{padding:10px;box-sizing:border-box;background:#fff;border:1px solid #ddd;border-radius:8px}
.restaurant-item img{width:100%;height:auto;border-radius:8px}
.restaurant-item__not__found{text-align:center}
.name{font-weight:500;font-size:16px;margin-top:10px;transition:0.3s opacity}
.name h1{font-weight:700;font-size:2em;margin-bottom:50px}
.name:hover{opacity:0.5}
.name a{text-decoration:none;color:inherit}
.daftar_item_desc{margin-top:10px;font-size:14px;line-height:1.5em}
.latest__label::after{content:"";margin-top:16px;display:block;border-bottom:1px solid #eeeeee}
.container-info{display:flex;flex-wrap:wrap;background-color:#fff;padding:20px;border-radius:8px;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2);margin-left:30px;margin-right:30px}
.img-container{flex:1 1 300px;text-align:center;margin-bottom:20px}
.detail-img{max-width:100%;border-radius:8px}
.detail-info{flex:2 1 500px;padding-left:40px}
.detail-info li{margin-bottom:10px;font-size:large}
.detail-item-name{font-size:24px;color:#333}
.detail-name-address,.detail-item-rating,.detail-item-desc{font-size:16px;color:#000}
.detail-item-rating{display:flex;align-items:center}
.detail-item-rating::before{margin-right:5px}
.resto-category{margin-top:10px}
.category{display:inline-block;background-color:#e0e0e0;padding:5px 10px;border-radius:4px;margin-right:5px;margin-bottom:5px}
.detail__menu-list{display:grid;grid-template-columns:repeat(2,1fr);gap:20px;margin-left:30px;margin-right:30px}
.foods,.drinks{background-color:#fff;padding:20px;border-radius:8px;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2)}
.detail__menu-list h3{margin-top:0}
.detail__menu-list ul{margin-top:10px;padding-left:20px}
.detail__menu-list ul li{margin-bottom:5px}
.font-decoration{margin-right:10px}
.detail-review{margin-left:30px;margin-right:30px}
.detail-review-item{background-color:#fff;padding:15px;border-radius:8px;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2);margin-bottom:10px}
.header-review{display:flex;justify-content:space-between;align-items:center}
.name-review{font-weight:bold;color:#333}
.date-review{color:#999}
.body-review{margin-top:10px;color:#555}
.form-review{background-color:#fff;padding:20px;border-radius:8px;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2);margin-top:20px;margin-left:30px;margin-right:30px;margin-bottom:30px}
.form-label{font-weight:bold}
.form-control{display:inline-block;width:100%;padding:14px;min-width:fit-content;min-height:fit-content;margin-top:10px;margin-bottom:15px;border:1px solid #ccc;border-radius:4px}
.btn{background-color:#2c3e50;color:#fff;padding:15px 25px;border:none;border-radius:4px;cursor:pointer}
.btn:hover{background-color:#496480}
.like{width:55px;height:55px;display:flex;align-items:center;justify-content:center;background-color:#2c3e50;position:fixed;bottom:16px;right:16px;border-radius:50%;border:0;font-size:18px;color:white;cursor:pointer}
footer{background-color:#2c3e50;padding:2em;width:100%;text-align:center}
footer p{text-decoration:none;color:#fff}
