@media screen and (max-width: 640px) {
  .hero {
    background-image: image-set(
    url(${___CSS_LOADER_URL_REPLACEMENT_1___}) 1x,
    url(${___CSS_LOADER_URL_REPLACEMENT_1___}) 2x
  );
